.app .szForm {
  padding-top: 32px;
  margin-bottom: 32px;
  box-sizing: border-box;
}
.app .form-title {
  margin-top: 32px;
  margin-bottom: 16px;
}

.app .szForm label {
  margin-bottom: 16px;
  background-color: white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02);
  padding: 0 0 0 12px;
  display: flex;
  align-items: center;
}
.app .szForm .no-bg {
  background-color: transparent;
  box-shadow: none;
  padding: 0;
}
.app .szForm label:focus-within {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02),
    0 0 3px var(--input-outline-hover);
}
.app .szForm label.no-bg:focus-within {
  box-shadow: none;
}

.app .szForm input {
  border: 0;
  padding: 14px 10px;
  flex-grow: 1;
}

.app .szForm [type="checkbox"] {
  width: 20px;
  flex-grow: 0;
}

.app .szForm .label-text {
  width: 100px;
  display: inline-block;
}

.app .szForm input:active {
  outline: none;
}

::placeholder {
  color: var(--grey-3);
}

.app textarea:focus,
.app input:focus {
  outline: none;
}

.app .szForm fieldset {
  border: 0;
  padding: 0;
}

.app .szForm .radio {
  display: block;
}

.app .szForm select {
  padding: 14px 10px;
  border: 0;
  width: 100%;
}

.app .szForm legend {
  font-weight: 700;
  margin-bottom: 0.8rem;
}
