.app .product {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white;
  /* max-height: var(--product-select-height); */
  box-sizing: border-box;
}

.app .product-thumb-wrapper {
  overflow: hidden;
  /* max-height: var(--product-select-height); */
}

.app img.product-thumb {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.app .product-listing {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
  column-gap: var(--product-select-gutter);
  row-gap: var(--product-select-gutter);
  height: 100%;
  grid-template-rows: repeat(auto-fill, minmax(300px, 450px));
}

.app .product.selected .product-short_description {
  opacity: 1;
}
.app .product-short_description {
  margin: 5px 0 0;
}
.app .product-listing .product {
  padding: 25px;
  border: 1px solid var(--grey-2);
  transition: box-shadow 0.5s;
  box-sizing: border-box;
}
.app .product-listing .selected.product {
  background-color: var(--white);
  border: 1px solid var(--accent-dark);
  box-shadow: 0px 0px 8px var(--box-shadow);
}

.app .product-listing .product:hover {
  cursor: pointer;
  border: 1px solid var(--black);
}
.app h3.product-title {
  font-size: 21px;
  margin-bottom: 0;
}
.app h4.product-price {
  margin: 0;
  font-weight: 600;
}
.app .product-bottom-line {
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
