.app .model-details-section {
  display: flex;
  background-color: white;
  padding: 2%;
  justify-content: space-around;
}
.app .model-details-section .model-details {
  flex-basis: var(--sidebar-width);
  overflow-y: scroll;
  max-height: 600px;
  margin-left: 2%;
}
.app .model-details-section .model-price {
  color: var(--accent-dark);
  font-size: 24px;
}
