.app .accessories-section {
  height: 100%;
  box-sizing: border-box;
}
.app .accessories-section-inner {
  display: flex;
  background-color: white;
  padding: 2%;
  height: 100%;
  box-sizing: border-box;
}
.app .model-image-container {
  flex-basis: calc(100% - var(--sidebar-width));
  display: flex;
  align-items: center;
  padding-right: 3%;
}

.app .sidebar-menu-1,
.app .sidebar-menu-2 {
  height: 100%;
  width: 100%;
  position: absolute;
  will-change: transform;
  overflow-y: scroll;
  padding: 0 6px;
}
.app .sidebar-menu-1 {
  transition: transform 300ms;
  transform: translateX(-100%);
}
.app .sidebar-menu-1.active {
  transform: translateX(0%);
}

.app .sidebar-menu-2 {
  transform: translateX(100%);
  transition: transform 300ms;
}
.app .sidebar-menu-2.active {
  transform: translateX(0%);
}

.app .accessories-sidebar {
  position: relative;
  overflow: hidden;
}
