.app .accordion-item-header {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  align-items: center;
}
.app .accordion-item-header h3,
.app .accordion-item-header path {
  color: var(--grey-4);
  fill: var(--grey-3);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-family: var(--heading-font);
}
.app .accordion-item-header:hover h3,
.app .accordion-item-header:hover path {
  color: var(--accent-dark);
  fill: var(--accent-dark);
}
.app .accordion-item .svg-icon-container {
  font-size: 22px;
}
.app .accordion-item .svg-icon-container svg {
  transition: transform 200ms;
}
.app .accordion-item.open .svg-icon-container svg {
  transform: scaleY(-100%);
}
.app .accordion-content-wrapper {
  overflow: hidden;
  transition: max-height 200ms;
  /* box-sizing: content-box; */
  padding: 0 6px;
  margin: 0 -6px;
  will-change: transform;
}
.app .accordion-item-content {
  padding-top: 5px;
  padding-bottom: 5px;
}
