@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap");
.current-screen-container {
  position: relative;
}
/* stop scrollbars ruining animation*/
.app {
  overflow: hidden;
}
.app .current-screen-container {
  height: calc(
    var(--product-select-rows) * var(--product-select-gutter) +
      var(--product-select-rows) * var(--product-select-height)
  );
}

.app .current-screen-container .item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}
.app .current-screen-container .item.active {
  position: static;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}
:root {
  --text: #373a47;
  --black: #373a47;
  --black-1: #282a33;

  --accent-dark: #e41212;
  --accent-dark-hover: #ff1d1d;
  --input-outline-hover: #ff1d1d;
  /* --accent-dark: #f26100; */
  /* --accent-dark-hover: rgba(242, 121, 0, 1); */
  /* --input-outline-hover: rgba(242, 121, 0, 1); */
  --white: #ffffff;
  --grey-1: #f6f7f9;
  --grey-1x: #e6e7e9;
  --grey-2: #dde0e6;
  --grey-3: #a5a9be;
  --grey-4: #9599ae;
  --grey-5: #7a7f97;
  --grey-6: #5a5f77;
  --grey-7: #4a4f67;
  --inverse-text: #dde0e6;
  --disabled: #cacaca;
  --header-height: 60px;
  --footer-height: 120px;
  --box-shadow: #a5a9beaa;
  --sidebar-width: 38%;
  --confirm: green;
  --warning: red;
  --heading-font: "Nunito", sans-serif;
  --body-font: "Nunito", sans-serif;
  --accessory-button-height: 68px;
  --product-select-gutter: 16px;
  --product-select-height: 312px;
  --product-select-rows: 2;
}
/* custom scrollbar */
::-webkit-scrollbar {
  width: 23px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: var(--grey-2);
  border-radius: 20px;
  border: 10px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

.app img {
  max-width: 100%;
}
.app {
  height: 100vh;
}
.primary-container {
  padding-top: 36px;
}

body .app {
  font-family: var(--heading-font);
  background-color: var(--grey-1);
  color: var(--black);
}

.app header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.app h1,
.app h2 {
  font-weight: 700;
  color: var(--text);
}

.app .inverse {
  background-color: var(--black);
}

.app .inverse h1,
.app h1.inverse,
.app .inverse h2,
.app h2.inverse,
.app .inverse h3,
.app h3.inverse,
.app .inverse h4,
.app h4.inverse,
.app .inverse h5,
.app h5.inverse,
.app .inverse h6,
.app h6.inverse,
.app .inverse p,
.app .inverse span {
  color: var(--inverse-text);
}

.app h1.has-subtitle,
.app h2.has-subtitle,
.app h3.has-subtitle,
.app h4.has-subtitle,
.app h5.has-subtitle,
.app h6.has-subtitle {
  margin-bottom: 3px;
}

.app h1.has-subtitle + .subtitle,
.app h2.has-subtitle + .subtitle,
.app h3.has-subtitle + .subtitle,
.app h4.has-subtitle + .subtitle,
.app h5.has-subtitle + .subtitle,
.app h6.has-subtitle + .subtitle {
  margin-top: 0;
}

.app h3 {
  margin: 0.5rem 0;
  font: Bold 18px/24px var(--heading-font);
  letter-spacing: 0px;
  color: var(--text);
}
.app h4,
.app h5,
.app h6 {
  font-weight: 600;
  margin: 0.5rem 0;
}
.app p {
  margin: 16px 0;
  font: normal 15px/20px var(--heading-font);
}
.app h1 {
  align-self: center;
  font-size: 18px;
  font-weight: 400;
}
.app .small-caps {
  text-transform: uppercase;
  font-size: 20px;
  color: var(--grey-5);
  font-weight: 600;
}
/**** reusable stuff. move this ***/
.app .text-button {
  background-color: transparent;
  display: inline;
  border: 0;
  padding: 0;
}
.app .text-button:hover {
  color: var(--accent-dark);
  cursor: pointer;
}
.app .primary-button:hover,
.app .secondary-button:hover {
  background-color: var(--accent-dark-hover);
  cursor: pointer;
}
.app .secondary-button:hover {
  border-color: var(--accent-dark-hover);
}

.app .primary-button.disabled,
.app .secondary-button.disabled {
  border-color: var(--disabled);
  background-color: var(--disabled);
  cursor: auto;
  pointer-events: none;
}

.app .primary-button {
  border: 1px solid var(--accent-dark);
  background-color: var(--accent-dark);
  color: var(--grey-1);
  display: flex;
  padding: 10px 20px;
  display: inline-flex;
  align-items: center;
  margin: 0 auto;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  font-family: "Nunito", sans-serif;
}

.app .secondary-button {
  border: 1px solid var(--grey-3);
  background-color: var(--grey-3);
  color: var(--grey-1);
  padding: 10px 20px;
  display: inline-flex;
  align-items: center;
  margin: 0 auto;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  font-family: "Nunito", sans-serif;
}

.app .outline-button {
  background-color: transparent;
  color: var(--accent-dark);
  padding: 10px 20px;
  border: 1px solid var(--accent-dark);
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
  font-family: "Nunito", sans-serif;
}

.app .primary-button svg,
.app .outline-button svg,
.app .secondary-button svg {
  margin-right: 5px;
  font-size: 20px;
}
.app .outline-button.secondary {
  color: var(--grey-5);
  border: 1px solid var(--grey-5);
}
.app .outline-button:hover {
  color: white;
  background-color: var(--accent-dark);
  border: 1px solid transparent;
}
.app .primary-button.small,
.app .outline-button.small,
.app .secondary-button.small {
  font-size: 14px;
  padding: 6px 12px;
}
.app .primary-link {
  font-size: 15px;
  font-weight: 600;
  color: var(--accent-dark);
  text-decoration: none;
}
.app .primary-link:hover {
  color: var(--accent-dark-hover);
}
.app .secondary-link {
  font-size: 15px;
  font-weight: 600;
  color: var(--grey-5);
  text-decoration: none;
}
.app .secondary-link:hover {
  color: var(--accent-dark);
}
.app .app-container {
  display: block;
  max-width: 1200px;
  margin: 0 auto;
  /* height: calc(100% - var(--header-height)); */
  height: 100%;
}

.app .primary-container main {
  padding: 10px;
}

.app .add-accessories a {
  font-weight: 900;
  text-decoration: none;
  color: var(--accent-dark);
  font-size: 22px;
  text-align: center;
}
.app .add-accessories svg,
.app .add-accessories span {
  vertical-align: middle;
}
.app .add-accessories:hover a {
  color: var(--accent-dark-hover);
}
.app .icon-line {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.app .add-icon {
  margin-right: 0.6rem;
  max-width: 24px;
  width: 24px;
  fill: var(--accent-dark);
  transition: all 0.3s;
}
.app .add-accessories:hover .add-icon {
  fill: var(--accent-dark-hover);
  transform: scale(1.2);
}
.app .add-accessories {
  margin: 1.5rem 0;
}

.app .wizard-footer {
  /* background-color: var(--grey-2); */
  /* padding: 16px; */
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.app .wizard-footer button {
  flex-grow: 0;
  margin: 0 2px;
}

.app .sidebar {
  flex-basis: var(--sidebar-width);
}
