@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
.app .accessory-details-sidebar {
  overflow-y: scroll;
}

.app .accessory-add-button {
  margin-bottom: 5px;
  border: 1px solid var(--grey-1x);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.07);
  display: flex;
  align-items: center;
  height: var(--accessory-button-height);
  box-sizing: border-box;
}
.app .accessory-add-button:hover {
  border: 1px solid var(--grey-2);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.15);
}
.app .accessory-add-button:hover .accessory-button-add-icon path {
  fill: var(--accent-dark);
}

.app .accessory-button-icon {
  height: var(--accessory-button-height);
  width: var(--accessory-button-height);
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid var(--grey-1x);
  cursor: pointer;
  font-size: 22px;
  color: var(--grey-3);
  flex-shrink: 0;
}
.app .accessory-button-icon:hover {
  color: var(--accent-dark);
  background-color: var(--grey-1);
}
.app .accessory-button-icon-added .accessory-button-icon-check {
  display: flex;
  color: var(--confirm);
}
.app .accessory-button-icon-added .accessory-button-icon-remove {
  display: none;
  color: var(--warning);
}
/* .accessory-button-icon-added:hover .accessory-button-icon-remove {
  display: flex;
} */
/* .accessory-button-icon-added:hover .accessory-button-icon-check {
  display: none;
} */

.app .accessory-button-thumb {
  width: var(--accessory-button-height);
  height: var(--accessory-button-height);
  /* margin-left: 4px; */
  flex-shrink: 0;
  cursor: pointer;
}
.app .accessory-button-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.app .accessory-button-label {
  flex-grow: 1;
  margin-left: 10px;
  cursor: pointer;
}

.app .accessory-button-title {
  font-size: 15px;
  font-weight: 300;
  line-height: 16px;
  display: block;
}

.app .accessory-button-subtitle {
  font-size: 14px;
  color: var(--grey-3);
  display: block;
  text-decoration: none;
  align-self: center;
}

.app .accessories-accordian-container {
  margin-left: 2%;
}
.app .accessory-button-info-wrapper {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.app .weak-label {
  color: var(--grey-3);
}
.app .accordian-category-name {
  color: var(--grey-4);
  font-size: 16px;
  display: block;
  font-weight: bold;
  letter-spacing: -0.5px;
}

.app .accessories-accordian {
  overflow-y: scroll;
  max-height: 600px;
}

.app .accordion-item-header {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  align-items: center;
}
.app .accordion-item-header h3,
.app .accordion-item-header path {
  color: var(--grey-4);
  fill: var(--grey-3);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-family: var(--heading-font);
}
.app .accordion-item-header:hover h3,
.app .accordion-item-header:hover path {
  color: var(--accent-dark);
  fill: var(--accent-dark);
}
.app .accordion-item .svg-icon-container {
  font-size: 22px;
}
.app .accordion-item .svg-icon-container svg {
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}
.app .accordion-item.open .svg-icon-container svg {
  -webkit-transform: scaleY(-100%);
          transform: scaleY(-100%);
}
.app .accordion-content-wrapper {
  overflow: hidden;
  transition: max-height 200ms;
  /* box-sizing: content-box; */
  padding: 0 6px;
  margin: 0 -6px;
  will-change: transform;
}
.app .accordion-item-content {
  padding-top: 5px;
  padding-bottom: 5px;
}

.app .accessories-section {
  height: 100%;
  box-sizing: border-box;
}
.app .accessories-section-inner {
  display: flex;
  background-color: white;
  padding: 2%;
  height: 100%;
  box-sizing: border-box;
}
.app .model-image-container {
  flex-basis: calc(100% - var(--sidebar-width));
  display: flex;
  align-items: center;
  padding-right: 3%;
}

.app .sidebar-menu-1,
.app .sidebar-menu-2 {
  height: 100%;
  width: 100%;
  position: absolute;
  will-change: transform;
  overflow-y: scroll;
  padding: 0 6px;
}
.app .sidebar-menu-1 {
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.app .sidebar-menu-1.active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.app .sidebar-menu-2 {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}
.app .sidebar-menu-2.active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.app .accessories-sidebar {
  position: relative;
  overflow: hidden;
}

.app .szForm {
  padding-top: 32px;
  margin-bottom: 32px;
  box-sizing: border-box;
}
.app .form-title {
  margin-top: 32px;
  margin-bottom: 16px;
}

.app .szForm label {
  margin-bottom: 16px;
  background-color: white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02);
  padding: 0 0 0 12px;
  display: flex;
  align-items: center;
}
.app .szForm .no-bg {
  background-color: transparent;
  box-shadow: none;
  padding: 0;
}
.app .szForm label:focus-within {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02),
    0 0 3px var(--input-outline-hover);
}
.app .szForm label.no-bg:focus-within {
  box-shadow: none;
}

.app .szForm input {
  border: 0;
  padding: 14px 10px;
  flex-grow: 1;
}

.app .szForm [type="checkbox"] {
  width: 20px;
  flex-grow: 0;
}

.app .szForm .label-text {
  width: 100px;
  display: inline-block;
}

.app .szForm input:active {
  outline: none;
}

::-webkit-input-placeholder {
  color: var(--grey-3);
}

:-ms-input-placeholder {
  color: var(--grey-3);
}

::-ms-input-placeholder {
  color: var(--grey-3);
}

::placeholder {
  color: var(--grey-3);
}

.app textarea:focus,
.app input:focus {
  outline: none;
}

.app .szForm fieldset {
  border: 0;
  padding: 0;
}

.app .szForm .radio {
  display: block;
}

.app .szForm select {
  padding: 14px 10px;
  border: 0;
  width: 100%;
}

.app .szForm legend {
  font-weight: 700;
  margin-bottom: 0.8rem;
}

.current-screen-container {
  position: relative;
}
/* stop scrollbars ruining animation*/
.app {
  overflow: hidden;
}
.app .current-screen-container {
  height: calc(
    2 * 16px +
      2 * 312px);
  height: calc(
    var(--product-select-rows) * var(--product-select-gutter) +
      var(--product-select-rows) * var(--product-select-height)
  );
}

.app .current-screen-container .item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}
.app .current-screen-container .item.active {
  position: static;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}
:root {
  --text: #373a47;
  --black: #373a47;
  --black-1: #282a33;

  --accent-dark: #e41212;
  --accent-dark-hover: #ff1d1d;
  --input-outline-hover: #ff1d1d;
  /* --accent-dark: #f26100; */
  /* --accent-dark-hover: rgba(242, 121, 0, 1); */
  /* --input-outline-hover: rgba(242, 121, 0, 1); */
  --white: #ffffff;
  --grey-1: #f6f7f9;
  --grey-1x: #e6e7e9;
  --grey-2: #dde0e6;
  --grey-3: #a5a9be;
  --grey-4: #9599ae;
  --grey-5: #7a7f97;
  --grey-6: #5a5f77;
  --grey-7: #4a4f67;
  --inverse-text: #dde0e6;
  --disabled: #cacaca;
  --header-height: 60px;
  --footer-height: 120px;
  --box-shadow: #a5a9beaa;
  --sidebar-width: 38%;
  --confirm: green;
  --warning: red;
  --heading-font: "Nunito", sans-serif;
  --body-font: "Nunito", sans-serif;
  --accessory-button-height: 68px;
  --product-select-gutter: 16px;
  --product-select-height: 312px;
  --product-select-rows: 2;
}
/* custom scrollbar */
::-webkit-scrollbar {
  width: 23px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #dde0e6;
  background-color: var(--grey-2);
  border-radius: 20px;
  border: 10px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

.app img {
  max-width: 100%;
}
.app {
  height: 100vh;
}
.primary-container {
