#sz-root .app .thankyou-wrapper .h1 {
  font-size: 36px;
  font-weight: bold;
  margin-top: 0;
  color: var(--grey-6);
}

.app .thankyou-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
  background-color: white;
  height: 100%;
  box-sizing: border-box;
  justify-content: center;
}

.app h3.thankyou-productname {
  font-size: 28px;
}
.app .thankyou-prod-name {
  color: var(--accent-dark);
}
.app .thankyou-accessories {
  font-size: 20px;
}
.app .thankyou-product-image img {
  height: 100%;
}
.app .thankyou-product-image {
  height: 250px;
}
.app .thankyou-box-price {
  font-size: 28px;
  font-weight: bold;
  color: var(--accent-dark);
}
.app .thankyou-buttons {
  margin-top: 16px;
}

.app .thankyou-buttons button {
  margin: 0 0.25rem 32px;
}
