.app .step-title-container {
  /* border-bottom: 1px solid var(--grey-2); */
  margin-bottom: 0.8rem;
  display: flex;
  overflow: hidden;
}

.app .step-title {
  margin-right: 32px;
}
.app .step-title h2 {
  font: 600 18px/30px Nunito, sans-serif;
  letter-spacing: -0.53px;
  color: var(--grey-3);
  white-space: nowrap;
  line-height: 24px;
  margin: 0;
}

.app .active.step-title {
  border-bottom: 2px solid var(--accent-dark);
}

.app .active.step-title h2 {
  color: var(--text);
  font-weight: bold;
}

