.app .build-box {
  border: 1px solid var(--grey-2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  box-sizing: border-box;
  /*jusnk style here to position */

  /* position: relative; */
  width: var(--sidebar-width);
}
.app .build-box-icon {
  font-size: 40px;
  margin-right: 6px;
  display: flex;
  align-items: center;
}
.app .build-box-icon svg {
  color: var(--grey-4);
  stroke-width: 1;
}
.app .build-box-left {
  display: flex;
  align-items: center;
}

.app .build-box-price {
  font-size: 25px;
  font-weight: 800;
  color: var(--grey-5);
}
body .app .build-box-title {
  margin: 0;
  margin-top: 6px;
  font-size: 26px;
  color: var(--accent-dark);
  line-height: 20px;
}
.app .build-box-accessories {
  color: var(--grey-5);
  font-size: 15px;
}
